export default {
  namespaced: true,
  state: {
    userInfo: {
      user: "",
      token: "",
    },
  },
  getters: {},
  mutations: {
    // 设置用户信息
    setUser(state, payload) {
      state.userInfo = payload;
    },
    clearUser(state) {
      state.userInfo = {
        user: "",
        token: "",
      };
    },
  },

  actions: {},
  getters: {},
};
